@import "./src/assets/sass/prepends.scss";

main {
  position: relative;
  background-color: $bg-light;
  > article {
    color: $text-black;
    > p {
      margin: 1rem 0;
      > em {
        font-weight: 800;
        font-style: normal;
      }
